
.ghem-logo {
  font-size: 3rem;
  position: absolute;
  top: 10px;
  line-height: 3rem;
  color: #fa8c16;
  -webkit-transition: all 500ms;
  transition: all 500ms;
  background: #fff;
  padding: 10px 15px; }
  .ghem-logo:hover {
    color: #fa541c; }

.border-grad, .ghem-logo {
  border: 5px solid orange;
  border-image-source: linear-gradient(orange, orangered);
  border-image-slice: 20; }

.header-scrolled .ghem-logo {
  font-size: 2rem;
  position: relative;
  top: auto;
  line-height: 2rem;
  background: transparent;
  border: none; }
  .header-scrolled .ghem-logo:focus {
    text-decoration: none; }

.nav-menu li a {
  font-weight: bold; }
  .nav-menu li a:focus {
    text-decoration: none; }

body {
  color: #777;
  font-family: "Poppins",sans-serif;
  font-size: 14px;
  font-weight: 300;
  line-height: 1.625em;
}

.special-font {
  font-family: 'Lobster', cursive;
}
.card-type {
  margin-bottom: 8px;
  font-weight: bold; }
  .card-type:hover {
    cursor: pointer; }

